import React, { Component } from 'react';

class Member extends Component {
    render() {
        return (
            <div className="col-lg-4">
                <div className="team-member">
                    <img className="mx-auto rounded-circle" src={this.props.src} alt="" />
                    <h4>{this.props.name}</h4>
                    <p className="text-muted">{this.props.position}</p>
                    <a className="btn btn-dark btn-social mx-2" href={this.props.mail}><i class="fas fa-envelope" /></a>
                    <a className="btn btn-dark btn-social mx-2" href={this.props.fb} target="_blank" rel="noreferrer"><i className="fab fa-facebook-f" /></a>
                    <a className="btn btn-dark btn-social mx-2" href={this.props.messenger} target="_blank" rel="noreferrer"><i className="fab fa-facebook-messenger" /></a>
                </div>
            </div>
        );
    }
}

export default Member;